*{
    box-sizing:  border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
    
}

body {
    background-color: var(--color-bg)
}

a {
    color: unset;
    text-decoration: none;

}
.gradient__bg {
    

        /* ff 3.6+ */
        background:-moz-radial-gradient(circle at 3% 25%, rgba(0, 40, 53, 1) 0%, rgba(4, 12, 24, 1) 100%);
    
        /* safari 5.1+,chrome 10+ */
        background:-webkit-radial-gradient(circle at 3% 25%, rgba(0, 40, 53, 1) 0%, rgba(4, 12, 24, 1) 100%);
    
        /* opera 11.10+ */
        background:-o-radial-gradient(circle at 3% 25%, rgba(0, 40, 53, 1) 0%, rgba(4, 12, 24, 1) 100%);
    
        /* ie 10+ */
        background:-ms-radial-gradient(circle at 3% 25%, rgba(0, 40, 53, 1) 0%, rgba(4, 12, 24, 1) 100%);
    
        /* global 92%+ browsers support */
        background:radial-gradient(circle at 3% 25%, rgba(0, 40, 53, 1) 0%, rgba(4, 12, 24, 1) 100%);
    
    }

    .gradient__text {
        background: var(--gradient-text);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
    }

    .section__padding {
        padding: 4rem 2rem;

    }
    .section__margin {
        margin: 4rem 2rem;

    }
